<template>
  <div class="container-fluid about-page">
    <h2> OUT.IM </h2>
    <p></p>
    <h2></h2>
    <ul>
      <li></li>
      <li></li>
      <li></li>
      <li></li>
    </ul>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'about',
  data () {
    return {
      msg: 'About'
    }
  }, 
  methods: mapActions([
    'increment',
    'decrement',
    'incrementIfOdd',
    'incrementAsync'
  ])
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.about-page {
  margin-top: 2rem
}
h1, h2 {
  font-weight: normal;
}


a {
  color: #42b983;
}
</style>



